import "core-js/modules/es.array.push.js";
import { reactive, toRefs } from "vue";
import FootListItem from "./FoodListItem.vue";
import ListItem from "../../../components/ListItem.vue";
export default {
  props: ["index", "foodData"],
  components: {
    FootListItem,
    ListItem
  },
  setup(props) {
    let data = reactive({
      activeIndex: 0,
      items: [],
      subItem: []
    });
    // 初始化数据
    const initData = () => {
      let newArr = [];
      props.foodData?.items?.forEach((item, index) => {
        newArr.push({
          text: item.text
        });
        //最后的subItem 是最后一项
        if (data.activeIndex === index) {
          data.subItem = item.children;
        }
      });
      data.items = newArr;
    };
    initData();
    const navclick = i => {
      console.log(i);
      data.activeIndex = i;
      initData();
    };
    const handAdd = id => {
      data.subItem.forEach((item, index) => {
        if (item.id === id) {
          item.add = false;
          item.num += 1;
        }
      });
    };
    const handlechange = (value, datail) => {
      data.subItem.forEach(item => {
        if (item.id === datail.name) {
          item.num = value;
        }
      });
      // console.log(data.subItem);
    };

    return {
      ...toRefs(data),
      navclick,
      handAdd,
      handlechange
    };
  }
};